import uCharts from '@/js_sdk/u-charts/u-charts/u-charts.js';
var _self;
	var canvaLineA=null;
export default{
	data(){
		return{
			active:1,
			active2:1,
			uid:'',
			token:'',
			data:'',
			sum:'',
			des:'累计订单量'
		}
	},
	mounted(){
		// this.init()
	},
	methods:{
		init(){
			_self = this;
			var info=uni.getSystemInfoSync()
						// uni.upx2px(750) 这是uni-app自带的自适应，以750的尺寸为基准。动态变化
						this.cWidth=info.windowWidth;
						var userInfo=uni.getStorageSync('userInfo')
						if(userInfo!='' & userInfo!=null & userInfo!=undefined){
							this.uid=userInfo.uid
							this.token=userInfo.token
							this.getBasicData()
						}
						this.getChartsData();
		},
		toggle(t){
			this.active=t
			this.active2=1
			if(t==1){
				this.des='累计订单量'
			}else if(t==2){
				this.des='产品出售量'
			}else if(t==3){
				this.des='店铺资金'
			}
			this.getChartsData()
		},
		toggle2(t){
			this.active2=t
			this.getChartsData()
		},
		getBasicData(){
			this.$loading()
			var that=this
			var url='&r=api.myshop.finance'
			var params={
				uid:this.uid,
				token:this.token
			}
			  this.$apiPost(url,params).then((res) =>{
				  uni.hideLoading()
				that.data=res.data	
			  })
		},
		getChartsData(){
			this.$loading()
			var that=this
			var url='&r=api.myshop.finance.totalOrder'
			var params={
				uid:this.uid,
				token:this.token,
				period:this.active2,
				type:this.active
			}
			  this.$apiPost(url,params).then((res) =>{
				  uni.hideLoading()
				  that.sum=res.data.sum
				  that.getServerData(res.data)
			  })
		},
		getServerData(data){
			let LineA={categories:[],series:[]};
								LineA.categories=data.day;
								LineA.series=[{
									name:this.des,
									data:data.nums,
									color:'#f56c6c'
								}]
								// LineA.series=[{
								// 	name:"南京",
								// 	data:[12,15,10,18,6,13],
								// 	color:'#409eff'
								// },{
								// 	name:"苏州",
								// 	data:[15,6,13,18,8,14],
								// 	color:'#e6a23c'
								// },{
								// 	name:"无锡",
								// 	data:[13,12,13,16,9,10],
								// 	color:'#f56c6c'
								// }];
								_self.showLineA("canvasLineA",LineA,data.minnum,data.maxnum);
					},
					// 展示图标的函数 接收参数，一个块的id,一个数据
					showLineA(canvasId,chartData,min,max){
						canvaLineA=new uCharts({
											$this:_self,
											canvasId: canvasId,
											type: 'line',
											fontSize:11,
											legend:{show:false,position:'top',float:'left'},
											dataLabel:false,
											dataPointShape:true,
											background:'#FFFFFF',
											pixelRatio:_self.pixelRatio,
											categories: chartData.categories,
											series: chartData.series,
											animation: true,
											xAxis: {
												type:'grid',
												gridColor:'#CCCCCC',
												gridType:'dash',
												dashLength:8,
												labelCount:4,
											},
											yAxis: {
												gridType:'dash',
												gridColor:'#CCCCCC',
												dashLength:8,
												// splitNumber:(max-min)/6,
												// min:min,
												max:max,
												// format:(val)=>{return val}
											},
											width: this.cWidth-10,
											height: 200,
											extra: {
												line:{
													type: 'straight'
												}
											}
										});
					},
					// 点击图表显示的内容
					touchLineA(e) {
						// 使用声明的变量canvaLineA
						canvaLineA.showToolTip(e, {
							format: function (item, category) {
								return category + ' ' + item.name + ':' + item.data 
							}
						});
					}
	}
}
